<template>
    <betting-event-wrap :event="event">
        <template #firstRowMiddle>
            <div class="score">
                <div class="score__concrete score_winner">{{ event.finalScore.team1 }}</div>
                <div class="score__separator">:</div>
                <div class="score__concrete">{{ event.finalScore.team2 }}</div>
            </div>
        </template>

        <template #secondRowTeam1>
            <div :class="badgeClasses(1)"></div>
        </template>

        <template #secondRowMiddle>
            <event-time-data :date-time="event.startDate" />
        </template>

        <template #secondRowTeam2>
            <div :class="badgeClasses(2)"></div>
        </template>

        <template #thirdRowTeam1>
            <div class="btn btn_result-disabled">
                <span v-if="isWinner(1)">win</span>
                <span v-else-if="!isWinner(2)">tie</span>
                <span v-else>loss</span>
            </div>
        </template>

        <template #thirdRowTeam2>
            <div class="btn btn_result-disabled">
                <span v-if="isWinner(2)">win</span>
                <span v-else-if="!isWinner(1)">tie</span>
                <span v-else>loss</span>
            </div>
        </template>
    </betting-event-wrap>
</template>

<script>
    import BettingEventMixin from './mixins/betting-event';
    import BettingEventWrap from "./BettingEventWrap";
    import EventTimeData from "../../EventTimeData/EventTimeData";

    export default {
        name: "BettingEventCompleted",
        components: {EventTimeData, BettingEventWrap},
        mixins: [BettingEventMixin],
        computed: {
            winnerNum() {
                if (this.isWinner(1)) return 1;
                if (this.isWinner(2)) return 2;

                return 0;
            }
        },
        methods: {
            isWinner(teamNum) {
                const teamKey = `team${teamNum}`;
                const teamId = this.event[teamKey]['id'];

                return teamId === this.event.winnerId;
            },

            badgeClasses(teamNum) {
                if (this.isWinner(teamNum)) {
                    return ['trophy', 'betting-event__winner'];
                } else {
                    return ['betting-event__loser'];
                }
            }
        }
    }
</script>

